import { AiOutlineMenu } from 'react-icons/ai';

const MenuToggle = ({ setPage }) => {
  return (
    <>
      <a type='button' className='navbar-brand' onClick={() => setPage('Home')}>
        Warmaster Pocket <small className='text-muted'> / v0.1.2</small>
      </a>
      <button className='navbar-toggler' data-toggle='collapse' data-target='#navbarContent'>
        <AiOutlineMenu />
      </button>
    </>
  );
};

export default MenuToggle;
