const ArmyBuilder = ({ currentList, currentCost, addAmount, removeAmount }) => {
  return (
    <table className='table align-middle'>
      <thead>
        <tr>
          <th>Amount</th>
          <th colspan='2'>Cost</th>
        </tr>
      </thead>
      <tbody>
        {currentList.map((e, index) =>
          e.amount > 0 ? (
            <>
              <tr>
                <td>
                  {e.amount} x {e.name}
                </td>
                <td>{e.amount * e.points} pts</td>
                <td className='text-right'>
                  <div className='btn-group'>
                    <button className={e.amount === e.max ? 'btn btn-sm btn-secondary disabled' : 'btn btn-sm btn-dark'} onClick={() => addAmount(index)}>
                      +
                    </button>
                    <button className={e.amount === e.min ? 'btn btn-sm btn-secondary disabled' : 'btn btn-sm btn-danger'} onClick={() => removeAmount(index)}>
                      –
                    </button>
                  </div>
                </td>
              </tr>
            </>
          ) : (
            ''
          )
        )}
      </tbody>
      <tfoot>
        <tr>
          <th className='text-right'>Total Cost:</th>
          <th colspan='2' className={currentCost > 2000 ? 'text-danger' : ''}>
            {currentCost} pts
          </th>
        </tr>
      </tfoot>
    </table>
  );
};

export default ArmyBuilder;
