import { GiMailedFist, GiPocketBow, GiHeartPlus, GiCheckedShield, GiBootPrints, GiFlyingFlag, GiDeathSkull, GiSnakeSpiral } from 'react-icons/gi';
import { BsFillCaretLeftFill } from 'react-icons/bs';
import { specialRules } from '../databases/ArmyRules';
const iconStyle = { marginBottom: '0.2em' };

const ArmyCards = ({ currentList, currentCost, addAmount, removeAmount }) => {
  return (
    <>
      {currentList.map((e, index) => (
        <div className='card border-white mb-2'>
          <div className='card-header bg-transparent d-flex px-0'>
            <button className='btn text-left flex-grow-1 collapsed' data-toggle='collapse' data-target={'#num' + index + '-collapse'}>
              {e.name} <small className='text-muted'>/ {e.supertype === 'upgrade' ? '+' + e.points : e.points} pts</small>
            </button>
            {e.amount === 0 ? (
              <button className='btn btn-sm btn-dark' onClick={() => addAmount(index)}>
                Include
              </button>
            ) : e.amount >= e.min ? (
              <button className='btn btn-sm btn-secondary disabled' onClick={() => removeAmount(index)}>
                Required
              </button>
            ) : (
              <button className='btn btn-sm btn-danger' onClick={() => removeAmount(index, true)}>
                Remove
              </button>
            )}
          </div>
          <div className='list-group list-group-flush collapse' id={'num' + index + '-collapse'}>
            <div className='list-group-item list-group-item-light d-flex'>
              <small className='flex-grow-1'>
                {e.size === 1 ? 'Single stand ' + e.type.join(' ') + ' ' + e.supertype + '.' : e.size === 2 ? 'Two stand ' + e.type.join(' ') + ' ' + e.supertype + '.' : e.size === 3 ? 'Three stand ' + e.type.join(' ') + ' ' + e.supertype + '.' : ''}
                {e.upgrades ? e.upgrades.map((f) => (f === 'Any' ? e.upgrades + ' character upgrade.' : e.upgrades + ' upgrade.')) : ''}
              </small>
              {e.unique ? <small>One per army</small> : <small>Max {e.max ? e.max : '∞'} per army</small>}
            </div>
            <li className='list-group-item'>
              {e.attack ? (
                <small className='pr-1'>
                  (<GiMailedFist style={iconStyle} /> {e.attack})
                </small>
              ) : (
                ''
              )}
              {e.range ? (
                <small className='px-1'>
                  (<GiPocketBow style={iconStyle} /> {e.range})
                </small>
              ) : (
                ''
              )}
              {e.hits ? (
                <small className='px-1'>
                  (<GiHeartPlus style={iconStyle} /> {e.hits})
                </small>
              ) : (
                ''
              )}
              {e.armour ? (
                <small className='px-1'>
                  (<GiCheckedShield style={iconStyle} /> {e.armour})
                </small>
              ) : (
                ''
              )}
              {e.move ? (
                <small className='px-1'>
                  (<GiBootPrints style={iconStyle} /> {e.move})
                </small>
              ) : (
                ''
              )}
              {e.command ? (
                <small className='px-1'>
                  (<GiFlyingFlag style={iconStyle} /> {e.command})
                </small>
              ) : (
                ''
              )}
            </li>
            {e.rules ? (
              e.rules.map((f) =>
                specialRules.map((g) =>
                  g.name === f ? (
                    <li className='list-group-item'>
                      <small className='card-text'>
                        <strong>{g.name}.</strong> {g.text}
                      </small>
                    </li>
                  ) : (
                    ''
                  )
                )
              )
            ) : (
              <li className='list-group-item'>
                <small className='card-text text-muted'>This {e.supertype} has no special rules.</small>
              </li>
            )}
          </div>
        </div>
      ))}
    </>
  );
};

export default ArmyCards;
