import { GiMailedFist, GiPocketBow, GiHeartPlus, GiCheckedShield, GiBootPrints, GiDeathSkull, GiSnakeSpiral, GiFlyingFlag } from 'react-icons/gi';
const iconStyle = { fontSize: '0.75em', marginBottom: '0.1em' };

const coreRules = [
  {
    name: 'Chapter 1',
    text: (
      <div>
        <h2 className='display-4 mb-3'>Introduction</h2>
        <p>Warmaster is a 10mm-scale miniature wargame. It's played on a tabletop with your buddies for fun.</p>
        <h3 className='lead'>Dice</h3>
        <p>This game uses six-sided dice, abbreviated as D6. When required to roll dice, always roll a D6. Two dice is abbreviated as 2D6, three dice as 3D6, etc. Whenever a D3 is required, roll a D6 and halve the result, rounding up.</p>
        <h3 className='lead'>Distances</h3>
        <p>This game measures all distances in metric.</p>
        <h3 className='lead'>Miniatures</h3>
        <p>Miniatures in warmaster are called stands, and each stand is based on a 2cm x 4cm rectangular base.</p>
        <h3>Where to Get Miniatures?</h3>
        <p>Use tokens or papercraft miniatures to get started playing as quickly as possible. You can try to find the ancient, original warmaster miniatures online from resellers if you're lucky. However, it's much easier to buy fresh 10mm fantasy miniatures from your local hobby store, from small online shops, or from friends with 3D printers.</p>
        <small>
          <p className='text-muted text-center'>
            If you want to include your minis storefront here, contact me via the{' '}
            <a className='link-secondary' href='https://discord.gg/pB2uR7kcvv' target='_blank'>
              Warmaster Revolution discord!
            </a>
          </p>
        </small>
      </div>
    ),
  },
  {
    name: 'Chapter 2',
    text: (
      <div>
        <h2 className='display-4 mb-3'>Getting Started</h2>
        <h3 className='lead'>Stands</h3>
        <p>Stands in warmaster come in two varieties: characters and units.</p>
        <p>
          <strong>Characters</strong> include your general, heroes, and wizards. These are stands are used to issue commands.
        </p>
        <p>
          <strong>Units</strong> include infantry, cavalry, chariots, monsters, artillery, and machines. These are groups of up to 3 stands that receive commands from characters to do things like moving, attacking, and shooting.
        </p>
        <h3 className='lead'>Stats</h3>
        <p>Units and characters have unique abilities called stats. Here's what each stat does:</p>
        <p>
          <strong>Attack</strong> <GiMailedFist style={iconStyle} /> are amount of attacks (D6s) this stand rolls during combat. Each stand in a unit contributes this number of attacks separately, so a unit of three stands with (<GiMailedFist style={iconStyle} /> 3) inflicts 3 x 3 attacks = 9 dice!
        </p>
        <p>
          Sometimes, this number has a slash like (<GiMailedFist style={iconStyle} /> 3/2) which means this stand also has a shooting attack. so a unit of three stands with (<GiMailedFist style={iconStyle} /> 3/2) are the same as above, however it also inflicts 3 x 2 shooting attacks = 6 dice!
        </p>
        <p>
          Rarely, this number will have a plus like (<GiMailedFist style={iconStyle} /> +2/+2), meaning this stand contributes an additional +2 attacks and +2 shooting attacks to its joined unit during combat. You'll see this on characters, as characters can join a combat as part of a unit.
        </p>
        <p>
          <strong>Range</strong> <GiPocketBow style={iconStyle} /> is the minimum distance required for this stand to make a shooting attack. Usually, its a distance in intervals of 5cm like (<GiPocketBow style={iconStyle} /> 30cm), meaning this stand can shoot the nearest enemy within 30cm.
        </p>
        <p>
          <strong>Hits</strong> <GiHeartPlus style={iconStyle} /> are the amount of successful attacks this stand can endure before it is destroyed. Most units have a number like (<GiHeartPlus style={iconStyle} /> 3), so a unit of three stands with can endure 3 x 3 hits = up to 9 attacks!
        </p>
        <p>Successful attacks are tracked throughout a battle, so each time a unit suffers from enough attacks to destroy a stand, you choose one of its stands to remove from play.</p>
        <p>
          <strong>Armour</strong> <GiCheckedShield style={iconStyle} /> Some units are armoured, meaning it can immediately roll a dice for each attack it suffered. If that roll is equals to or greater than its listed armour, that attack is unsuccessful.
        </p>
        <p>
          Armour is written to show which number needs to be rolled, so an armour of (<GiCheckedShield style={iconStyle} /> 4+) will render attacks unsuccessful on a roll a 4 or higher.
        </p>
        <p>
          <strong>Move</strong> <GiBootPrints style={iconStyle} /> The maximum distance this stand can move across the tabletop. Like shooting range, move is a distance in intervals of 5cm like (<GiBootPrints style={iconStyle} /> 20cm), meaning this stand can move from its current location up to 20cm in any direction, ending its movement facing any direction.
        </p>
        <p>
          <strong>Command</strong> <GiFlyingFlag style={iconStyle} /> Only characters have this stat, and its written as a number and a distance like (<GiFlyingFlag style={iconStyle} /> 7 | 60cm), meaning this character can command units within 60cm although it requires a special command roll of 7 or less to be successful (more on this later).
        </p>
        <h3 className='lead'>Armies</h3>
        <p>Armies are 2000 pts and must include all minimum required unit choices for 2000 pts games. Each army list has different minimum requirements, so be sure to check before playing.</p>
        <h3>Anything Else to Learn?</h3>
        <p>Get familiar with how a stand looks by checking out the armies section of this app. You can see how the different stands share similar themes across armies. See if any army inspires you to collect them!</p>
        <p className='text-muted text-center'>
          <small>Next chapter coming soon.</small>
        </p>
      </div>
    ),
  },
];
export default coreRules;
