import coreRules from '../databases/CoreRules';
import { armies } from '../databases/ArmyRules';

const Navigation = ({ setPage }) => {
  return (
    <div className='collapse navbar-collapse' id='navbarContent'>
      <div className='navbar-nav'>
        <button className='btn nav-link bg-light mb-1' data-toggle='collapse' data-target='.navbar-collapse.show' onClick={() => setPage('Home')}>
          Home
        </button>
        <button className='btn nav-link bg-light mb-1' data-toggle='collapse' data-target='#rulesContent'>
          Rules
        </button>
        <div className='collapse navbar-collapse' id='rulesContent'>
          <div className='navbar-nav'>
            {coreRules.map((e) => (
              <button className='btn nav-link bg-dark text-light mb-1' data-toggle='collapse' data-target='.navbar-collapse.show' onClick={() => setPage(e.name)}>
                {e.name}
              </button>
            ))}
          </div>
        </div>
        <button className='btn nav-link bg-light mb-1' data-toggle='collapse' data-target='#armyContent'>
          Armies
        </button>
        <div className='collapse navbar-collapse' id='armyContent'>
          <div className='navbar-nav'>
            {armies.map((e) => (
              <button className='btn nav-link bg-dark text-light mb-1' data-toggle='collapse' data-target='.navbar-collapse.show' onClick={() => setPage(e.name)}>
                {e.name} {e.name === 'Skaven' ? <sup className='px-1 mx-1 rounded-pill bg-danger'>New</sup> : ''}
              </button>
            ))}
          </div>
        </div>
        <button className='btn nav-link bg-light mb-1' data-toggle='collapse' data-target='.navbar-collapse.show' onClick={() => setPage('Faq')}>
          FAQ
        </button>
        <button className='btn nav-link bg-light mb-1' data-toggle='collapse' data-target='.navbar-collapse.show' onClick={() => setPage('Credits')}>
          Credits
        </button>
      </div>
    </div>
  );
};

export default Navigation;
