import { motion } from 'framer-motion';

const Page = (props) => {
  return (
    <motion.div initial={{ opacity: 0, x: 20 }} animate={{ opacity: 1, x: 0 }}>
      <main className='container pb-5 mb-5'>
        <h2>{props.title}</h2>
        <hr />
        <article>{props.children}</article>
      </main>
    </motion.div>
  );
};

export default Page;
