import { useState, useEffect } from 'react';
import ArmyBuilder from './ArmyBuilder';
import ArmyCards from './ArmyCards';

const ArmyData = ({ army }) => {
  // Create an empty army list based on its database entry, except each unit now has an Amount property
  const [currentList, setCurrentList] = useState(army.units.map((e) => ({ ...e, amount: e.min ? e.min : 0 })));

  const increaseAmount = (index) => {
    const updatedList = currentList.map((e, i) => ({ ...e, amount: i === index ? e.amount + 1 : e.amount }));
    setCurrentList(updatedList);
  };
  const decreaseAmount = (index, removeAll) => {
    if (removeAll) {
      const updatedList = currentList.map((e, i) => ({ ...e, amount: i === index ? 0 : e.amount }));
      setCurrentList(updatedList);
    } else {
      const updatedList = currentList.map((e, i) => ({ ...e, amount: i === index ? e.amount - 1 : e.amount }));
      setCurrentList(updatedList);
    }
  };

  const [currentCost, setCurrentCost] = useState(0);
  useEffect(() => {
    const sumCost = currentList.map((e) => e.amount * e.points);
    const reducer = (accumulator, currentValue) => accumulator + currentValue;
    setCurrentCost(sumCost.reduce(reducer));
  }, [currentList]);

  return (
    <>
      <ArmyCards currentList={currentList} currentCost={currentCost} addAmount={increaseAmount} removeAmount={decreaseAmount} />
      <div className='px-2 my-4'>
        <h3>Army Builder</h3>
        <p>A 2000 pts army of {army.name} has the following minimum requirements:</p>
        <ArmyBuilder currentList={currentList} currentCost={currentCost} addAmount={increaseAmount} removeAmount={decreaseAmount} />
        <p className='text-center mb-5'>
          {currentCost > 2000 ? (
            <>
              You've gone <strong className='text-danger'>{currentCost - 2000} pts</strong> over budget!
            </>
          ) : (
            <>
              You have <strong>{2000 - currentCost} pts</strong> left to spend on your army!
            </>
          )}
        </p>
      </div>
    </>
  );
};

export default ArmyData;
